import React, { useState, useEffect, useCallback } from 'react';
import { Button, Drawer } from '@material-ui/core';
import { Add } from '@material-ui/icons';
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";
import { useToasts } from 'react-toast-notifications';

import Card from '../components/card';
import NoCard from '../components/nocard';
import Layout from './layout';
import api from '../api';
import CheckoutForm from '../components/Payment/CheckoutForm';
import QuickView from '../components/quickview';

import { STRIPE_PUBLIC_KEY } from "../config";
const stripePromise = loadStripe(STRIPE_PUBLIC_KEY);

const Purchase = (props) => {
  const [limit] = useState(12);
  const [skip, setSkip] = useState(0);
  const [size, setSize] = useState(0);
  const [cards, setCards] = useState([]);
  const [quickViewCardIdx, setQuickViewCardIdx] = useState(-1);
  const [openPayment, setOpenPayment] = useState(false);
  const [clientSecret, setClientSecret] = useState("");

  const { addToast } = useToasts();

  const showToast = useCallback((message, appearance = 'error') => {
    addToast(message, { appearance, autoDismiss: true });
  }, [addToast])

  useEffect(() => {
    const loadCards = () => {
      api.getProducts({ skip: 0, limit, purchased: 1 })
        .then((data) => {
          setCards(data.data);
          setSize(data.size);
          setSkip(0);
        })
        .catch(err => showToast(err.message))
    };

    loadCards();
    
    const clientSecret = new URLSearchParams(window.location.search).get(
      "payment_intent_client_secret"
    );

    if (!clientSecret) {
      return;
    }
    setClientSecret(clientSecret);
    setOpenPayment(true);
  }, [limit, showToast])

  const loadMore = () => {
    let toSkip = skip + limit;
    api.getProducts({ skip: toSkip, limit, purchased: 1 })
      .then((data) => {
        setCards([...cards, ...data.data]);
        setSize(data.size);
        setSkip(toSkip);
      })
      .catch(err => showToast(err.message))
  };

  const loadMoreButton = () => {
    return (
      size > 0 &&
      size >= limit && (
        <Button onClick={loadMore} variant='contained' className='more-btn'>
          more <Add />
        </Button>
      )
    );
  };

  const updateProduct = (_id, detail) => {
    api.updateProduct(_id, detail)
      .then((data) => {
        const idx = cards.findIndex(itm => itm._id === data._id)
        if (idx < 0) return
        cards[idx] = data;
        setCards([...cards]);
      })
      .catch(err => showToast(err.message))
  }
  
  const removeClicked = (card) => {
    api.removeProduct(card._id)
      .then(() => {
        const idx = cards.findIndex(itm => itm._id === card._id)
        if (idx < 0) return
        if (idx === quickViewCardIdx && idx === cards.length - 1) setQuickViewCardIdx(idx - 1);
        cards.splice(idx, 1);
        setCards([...cards]);
      })
      .catch(err => showToast(err.message));
  }

  const removeCartClicked = (card) => {
    putCart(card._id, card.shared, 0);
    
    const idx = cards.findIndex(itm => itm._id === card._id)
    if (idx < 0) return;
    cards.splice(idx, 1);
    setCards([...cards]);
  }

  const putCart = (_id, shared, purchased) => {
    updateProduct(_id, { shared, purchased });
  }

  const save = (card) => {
    updateProduct(card._id, { description: card.description })
  }
  
  const shareClicked = (card) => {
    putCart(card._id, (card.shared ? 0 : 1), card.purchased);
  }
  const putPurchaseClicked = (card) => {
    putCart(card._id, card.shared, (card.purchased ? 0 : 1));
  }

  const quickView = (idx) => {
    setQuickViewCardIdx(idx);
  }
  const previousQuickView = () => {
    if (quickViewCardIdx < 1) return;
    setQuickViewCardIdx(quickViewCardIdx - 1);
  }
  const nextQuickView = () => {
    if (quickViewCardIdx > cards.length - 2) {
      loadMore();
      return ;
    }
    setQuickViewCardIdx(quickViewCardIdx + 1);
  }
  
  const createPaymentIntent = () => {
    api.createPaymentIntent()
      .then((data) => {
        setClientSecret(data.clientSecret);
      })
      .catch(err => showToast(err.message));
  }

  const togglePayment = (value) => {
    setOpenPayment(value);
    if (value) createPaymentIntent();
  }

  return (
    <Layout>
      {cards.filter(itm => itm.purchased).length && <div className='input-button purchase-button' onClick={() => togglePayment(true)}>
        Purchase items
      </div>}
      <Drawer style={{zIndex: 999}} anchor='right' open={openPayment} onClose={() => togglePayment(false)}>
        { clientSecret &&
          <Elements options={{clientSecret, appearance: { theme: 'stripe' }}} stripe={stripePromise}>
            <CheckoutForm clientSecret={clientSecret} />
          </Elements> }
      </Drawer>
      <div>
        <div className='d-flex flex-wrap justify-content-center'>
          {cards.filter(itm => itm.purchased).map((card, i) => (
            <div key={i} className={'shop-item ' + (card.shared ? 'shared ' : '' ) + (card.purchased ? 'purchased ' : '' )} onClick={() => quickView(i)}>
              <Card card={card} remove={() => removeCartClicked(card)} quickView={() => quickView(i)}/>
            </div>
          ))}
        </div>
        {loadMoreButton()}
      </div>
      {!cards.filter(itm => itm.purchased).length &&<NoCard page="purchase" />}
      {(quickViewCardIdx > -1) && <QuickView card={cards[quickViewCardIdx]}
        editable={true}
        previous={previousQuickView}
        next={nextQuickView}
        share={() => shareClicked(cards[quickViewCardIdx])}
        save={save}
        remove={() => removeClicked(cards[quickViewCardIdx])}
        putPurchase={() => putPurchaseClicked(cards[quickViewCardIdx])}
        close={()=>setQuickViewCardIdx(-1)} />}
    </Layout>
  );
};

export default Purchase;
