import React, { useState, useEffect, useCallback } from 'react';
import Layout from './layout'
import { Typography, Box, Avatar, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Pagination from '@material-ui/lab/Pagination';
import { useToasts } from 'react-toast-notifications';
import api from '../../api';

const useStyles = makeStyles(() => ({
  tableBox: {
    marginTop: 25
  },
  table: {
    width: '100%'
  },
  avatar: {
    color: 'var(--fg)',
    backgroundColor: 'orangered'
  },
  paginationWrapper: {
    marginTop: '10px',
    display: 'flex',
    justifyContent: 'center'
  }
}));

const Users = () => {
  const classes = useStyles();
  const { addToast } = useToasts();

  const [ users, setUsers ] = useState([]);
  const [countPerPage] = useState(10);
  const [totalCount, setTotalCount] = useState(1);
  const [page, setPage] = useState(1);
  
  const showToast = useCallback((message, appearance = 'error') => {
    addToast(message, { appearance, autoDismiss: true });
  }, [addToast]);

  const fetchUsers = useCallback(() => {
    api.getUsers({ page, countPerPage })
      .then((data) => {
        console.log(data)
        setUsers(data.users);
        setTotalCount(Math.floor((data.total - 1) / countPerPage) + 1);
      })
      .catch(err => showToast(err.message));
  }, [page, countPerPage, showToast])

  useEffect(() => {
    fetchUsers();
  }, [page, fetchUsers])

  const handlePageChange = (event, value) => {
    setPage(value);
  };

  return (
    <Layout>
      <Box marginY={1}>
        <Typography variant='h4'>OllaCart Users</Typography>
      </Box>
      <TableContainer component={Paper} className={classes.tableBox}>
        <Table stickyHeader className={classes.table} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell width={80}></TableCell>
              <TableCell>Email</TableCell>
              <TableCell align="center">Role</TableCell>
              <TableCell align="center">Extension ID</TableCell>
              <TableCell align="center">Carts</TableCell>
              <TableCell align="center">Shared</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {users.map((user, idx) => (
              <TableRow key={idx}>
                <TableCell><Avatar className={classes.avatar}>{user.email[0].toUpperCase()}</Avatar></TableCell>
                <TableCell>{user.email}</TableCell>
                <TableCell align="center">{(user.role === 'admin') ? 'Administrator' : 'User'}</TableCell>
                <TableCell align="center">{user.ce_id}</TableCell>
                <TableCell align="center"></TableCell>
                <TableCell align="center"></TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <Box className={classes.paginationWrapper}>
        <Pagination count={totalCount} page={page} onChange={handlePageChange} color="primary" showFirstButton showLastButton />
      </Box>
    </Layout>
  )
}

export default Users;