import { Constants } from '../_constants';
import api from '../../api';
import utils from '../../utils';

export const actions = {
  signin,
  verifySignin,
  signout,
  verify,
  setSecure,
  setError,
  setLoading,
}

function signin(data) {
  return dispatch => {
    let { email } = data;
    email = (email || '').replace(/ /g, '').toLocaleLowerCase();
    if (!email) return dispatch(setError('Input valid email!'));
    if (!utils.validateEmail(email)) return dispatch(setError('Input valid email!'));
    dispatch(setLoading(true));

    api.signin(email)
      .then(data => {
        dispatch(setLoading(false));
        if(data.verify) {
          return dispatch({
            type: Constants.VERIFYEMAIL,
            secure_identity: data.uid
          })
        }
        if (!data || !data.user || data.error) {
          return dispatch({
            type: Constants.SIGNIN_FAILED,
            error: (data && data.error) || 'Signin Failed!'
          })
        }
        localStorage.setItem('token', data.token);
        dispatch({
          type: Constants.SIGNIN_SUCCESS,
          email: data.user.email,
          role: data.user.role,
          _id: data.user._id,
          secure: data.user.secure
        });
      });
  }
}

function verifySignin(data) {
  return dispatch => {
    let { uid } = data;
    dispatch(setLoading(true));

    api.verifySignin(uid)
      .then(data => {
        dispatch(setLoading(false));
        if (!data || data.error) {
          return dispatch({
            type: Constants.SIGNIN_FAILED,
            error: (data && data.error) || 'Signin Failed!'
          })
        }
        localStorage.setItem('token', data.token);
        window.location.href = '/home';
      });
  }
}

function verify(token) {
  return dispatch => {
    dispatch(setVerifyLoading('loading'));
    if(!token) {
      return dispatch(setVerifyLoading('finished'));
    }
    api.verifyUser(token)
      .then(data => {
        if (!data || data.error) {
          localStorage.setItem('token', '');
          return ;
        }
        localStorage.setItem('token', data.token);
        dispatch({
          type: Constants.SIGNIN_SUCCESS,
          email: data.user.email,
          role: data.user.role,
          _id: data.user._id,
          secure: data.user.secure
        });
        dispatch(setVerifyLoading('finished'))
      });
  }
}

function signout() {
  localStorage.removeItem('token')
  return { type: Constants.SIGNOUT }
}

function setSecure(secure = true) {
  return {
    type: Constants.SETSECURE,
    secure
  }
}

function setLoading(loading = true) {
  return {
    type: Constants.SETLOADING,
    loading
  }
}

function setVerifyLoading(state = 'finished') {
  return {
    type: Constants.SETVERIFYLOADING,
    state
  }
}

function setError(error) {
  return {
    type: Constants.SETERROR,
    error
  }
}