import React, { useState, useEffect, useCallback } from 'react';
import { useSelector } from "react-redux"
import copy from 'copy-to-clipboard';
import { Button } from '@material-ui/core';
import { FileCopyOutlined, Add, Favorite, FavoriteBorder } from '@material-ui/icons';
import { useToasts } from 'react-toast-notifications';
import Card from '../components/card';
import NoCard from '../components/nocard';
import Layout from './layout';
import api from '../api';
import { SHARE_URL } from '../config';
import QuickView from '../components/quickview';
import Modal from '../components/modal';
import utils from '../utils';

const Share = (props) => {
  const [limit] = useState(12);
  const [skip, setSkip] = useState(0);
  const [size, setSize] = useState(0);
  const [cards, setCards] = useState([]);
  const [sharedId, setSharedId] = useState('');
  const [sharedEmail, setSharedEmail] = useState('');
  const [quickViewCardIdx, setQuickViewCardIdx] = useState(-1);
  const [followStatus, setFollowStatus] = useState(false);
  const [followedCount, setFollowedCount] = useState(0);
  const [inputEmail, setInputEmail] = useState('');
  const [showEmailModal, setShowEmailModal] = useState(false);

  const { email, _id } = useSelector(state => state.auth);
  const { addToast } = useToasts();
  
  const showToast = useCallback((message, appearance = 'error') => {
    addToast(message, { appearance, autoDismiss: true });
  }, [addToast])

  const fetchFollowingStatus = useCallback(() => {
    if (!sharedId) return;
    api.getFollowStatus(sharedId)
      .then(data => {
        setFollowStatus(data.status);
        setFollowedCount(data.count);
      })
      .catch(err => showToast(err.message))
  }, [sharedId, showToast])

  useEffect(() => {
    const userid = props.match.params.userid;
    setSharedId(userid);
  }, [props]);

  useEffect(() => {
    if (!sharedId) return;
    const loadCards = () => {
      api.getProducts({ skip: 0, limit, shared: 1, _id: sharedId })
        .then((data) => {
          setCards(data.data);
          setSharedEmail(data.email);
          setSize(data.size);
          setSkip(0);
        })
        .catch((err) => showToast(err.message));
    }
    loadCards();
  }, [sharedId, limit, showToast]);

  useEffect(() => {
    if (!sharedEmail) return
    fetchFollowingStatus();
  }, [sharedEmail, fetchFollowingStatus]);

  const loadMore = () => {
    let toSkip = skip + limit;
    api.getProducts({ skip: toSkip, limit, shared: 1, _id: sharedId })
      .then((data) => {
        setCards([...cards, ...data.data]);
        setSize(data.size);
        setSkip(toSkip);
      })
      .catch(err => showToast(err.message));
  };

  const loadMoreButton = () => {
    return (
      size > 0 &&
      size >= limit && (
        <Button onClick={loadMore} variant='contained' className='more-btn'>
          more <Add />
        </Button>
      )
    );
  };

  const copyShareUrl = () => {
    copy(`${SHARE_URL}/${_id}`);
  }

  const updateProduct = (_id, detail) => {
    api.updateProduct(_id, detail)
      .then((data) => {
        const idx = cards.findIndex(itm => itm._id === data._id)
        if (idx < 0) return
        cards[idx] = data;
        setCards([...cards]);
      })
      .catch(err => showToast(err.message));
  }
  
  const removeClicked = (card) => {
    api.removeProduct(card._id)
      .then(() => {
        const idx = cards.findIndex(itm => itm._id === card._id)
        if (idx < 0) return;
        if (idx === quickViewCardIdx && idx === cards.length - 1) setQuickViewCardIdx(idx - 1);
        cards.splice(idx, 1);
        setCards([...cards]);
      })
      .catch(err => showToast(err.message))
  }

  const removeCartClicked = (card) => {
    putCart(card._id, 0, card.purchased);
    
    const idx = cards.findIndex(itm => itm._id === card._id)
    if (idx < 0) return;
    cards.splice(idx, 1);
    setCards([...cards]);
  }

  const putCart = (_id, shared, purchased) => {
    updateProduct(_id, { shared, purchased });
  }

  const save = (card) => {
    updateProduct(card._id, { description: card.description })
  }
  
  const shareClicked = (card) => {
    putCart(card._id, (card.shared ? 0 : 1), card.purchased);
  }
  const putPurchaseClicked = (card) => {
    putCart(card._id, card.shared, (card.purchased ? 0 : 1));
  }

  const quickView = (idx) => {
    setQuickViewCardIdx(idx);
  }
  const previousQuickView = () => {
    if (quickViewCardIdx < 1) return;
    setQuickViewCardIdx(quickViewCardIdx - 1);
  }
  const nextQuickView = () => {
    if (quickViewCardIdx > cards.length - 2) {
      loadMore();
      return ;
    }
    setQuickViewCardIdx(quickViewCardIdx + 1);
  }

  const followUser = () => {
    api.followUser(sharedId)
      .then(fetchFollowingStatus)
      .catch(err => showToast(err.message))
  }

  const followUserWithEmail = () => {
    const email = (inputEmail || '').replace(/ /g, '').toLocaleLowerCase();
    if (!email) return showToast('Input valid email address');
    if (!utils.validateEmail(email)) return showToast('Input valid email address');

    api.followUser(sharedId, email)
      .then(() => {
        setShowEmailModal(false);
        fetchFollowingStatus();
      })
      .catch(err => showToast(err.message))
  }

  const unFollowUser = () => {
    api.unFollowUser(sharedId)
      .then(fetchFollowingStatus)
      .catch(err => showToast(err.message))
  }

  const followClicked = () => {
    if (sharedEmail === email) return;
    if (!email) {
      setShowEmailModal(true);
      return;
    }
    if (!followStatus) followUser();
    else unFollowUser();
  }

  return (
    <Layout sharedEmail={sharedEmail}>
      {sharedEmail === email && email && <div className='shared-header'>
        <div className='shared-url'>ollacart.com/share/{_id}<FileCopyOutlined onClick={copyShareUrl}/></div>
      </div>}
      <div className='follow-button' onClick={followClicked}>
        <span>Followers: {followedCount}</span>
        {sharedEmail !== email && !followStatus && <FavoriteBorder /> }
        {sharedEmail !== email && followStatus && <Favorite /> }
      </div>
      <div>
      {sharedEmail !== email ? (
        <div className='d-flex flex-wrap justify-content-center'>
          {cards.map((card, i) => (
            <div key={i} className='shop-item' onClick={() => quickView(i)}>
              <Card card={card}/>
            </div>
          ))}
        </div> ) : (
        <div className='d-flex flex-wrap justify-content-center'>
          {cards.filter(itm => itm.shared).map((card, i) => (
            <div key={i} className={'shop-item ' + (card.shared ? 'shared ' : '' ) + (card.purchased ? 'purchased ' : '' )} onClick={() => quickView(i)}>
              <Card card={card} remove={() => removeCartClicked(card)} quickView={() => quickView(i)}/>
            </div>
          ))}
        </div> )}
        {loadMoreButton()}
      </div>
      {!cards.length &&<NoCard page="share" />}
      {(quickViewCardIdx > -1) && <QuickView card={cards[quickViewCardIdx]}
        editable={sharedEmail === email}
        previous={previousQuickView}
        next={nextQuickView}
        share={() => shareClicked(cards[quickViewCardIdx])}
        save={save}
        remove={() => removeClicked(cards[quickViewCardIdx])}
        putPurchase={() => putPurchaseClicked(cards[quickViewCardIdx])}
        close={()=>setQuickViewCardIdx(-1)} />}
      {!email && <Modal open={showEmailModal} onClose={() => setShowEmailModal(false)}>
        <div className="input-label"> Input your email address </div>
        <div className="input-text">
          <input value={inputEmail} onChange={(e) => setInputEmail(e.target.value)}/>
        </div>
        <button className="input-button" onClick={followUserWithEmail}>
          Follow
        </button>
      </Modal>}
    </Layout>
  );
};

export default Share;
