import React from 'react';
import { Modal } from "@material-ui/core"

const ModalCompoent = ({ children, open, onClose }) => {
  return (
    <Modal className='ollacart-modal' open={open} onClose={onClose}>
      <div style={{backgroundColor: 'var(--bg)', padding: '10px', border: '10px solid var(--primary)'}}>
        {children}
      </div>
    </Modal>
  )
}

export default ModalCompoent;